// src/redux/slices/taskSlice.js
import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import { fetchProjectTasks, fetchProjects, loginUser } from '../../api';

export const fetchTasks = createAsyncThunk('task/fetchTasks', async (projectId, { rejectWithValue }) => {
  try {
    const response = await API.get(`/api/tasks/project/${projectId}`);
    return response.data;
  } catch (err) {
    return rejectWithValue(err.response.data.message || 'Failed to fetch tasks');
  }
});

export const createTask = createAsyncThunk('task/createTask', async (taskData, { rejectWithValue }) => {
  try {
    const response = await API.post('/api/tasks', taskData);
    return response.data;
  } catch (err) {
    return rejectWithValue(err.response.data.message || 'Failed to create task');
  }
});

const taskSlice = createSlice({
  name: 'task',
  initialState: {
    tasks: [],
    currentTask: null,
    loading: false,
    error: null,
  },
  reducers: {
    setCurrentTask(state, action) {
      state.currentTask = action.payload;
    },
    updateTaskStatus(state, action) {
      const { taskId, status } = action.payload;
      const task = state.tasks.find((t) => t._id === taskId);
      if (task) task.status = status;
    },
  },
  extraReducers: (builder) => {
    builder
      // Fetch Tasks
      .addCase(fetchTasks.pending, (state) => {
        state.loading = true;
      })
      .addCase(fetchTasks.fulfilled, (state, action) => {
        state.tasks = action.payload;
        state.loading = false;
      })
      .addCase(fetchTasks.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      })
      // Create Task
      .addCase(createTask.pending, (state) => {
        state.loading = true;
      })
      .addCase(createTask.fulfilled, (state, action) => {
        state.tasks.push(action.payload);
        state.loading = false;
      })
      .addCase(createTask.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      });
  },
});

export const { setCurrentTask, updateTaskStatus } = taskSlice.actions;
export default taskSlice.reducer;
