import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import axios from 'axios'; // Using axios for API requests
import { fetchProjectTasks, fetchProjects as apiFetchProjects } from '../../api'; // Correct imports

// Fetch all projects
export const fetchProjects = createAsyncThunk('project/fetchProjects', async (_, { rejectWithValue }) => {
  try {
    const response = await apiFetchProjects();
    return response.data;
  } catch (err) {
    return rejectWithValue(err.response?.data?.message || 'Failed to fetch projects');
  }
});

// Create a new project
export const createProject = createAsyncThunk('project/createProject', async (projectData, { rejectWithValue }) => {
  try {
    const response = await axios.post('https://asana-backend.onrender.com/api/projects', projectData); // Use axios for API requests
    return response.data;
  } catch (err) {
    return rejectWithValue(err.response?.data?.message || 'Failed to create project');
  }
});

const projectSlice = createSlice({
  name: 'project',
  initialState: {
    projects: [], // Initialize projects as an empty array
    currentProject: null,
    loading: false,
    error: null,
  },
  reducers: {
    setCurrentProject(state, action) {
      state.currentProject = action.payload;
    },
  },
  extraReducers: (builder) => {
    builder
      // Fetch Projects
      .addCase(fetchProjects.pending, (state) => {
        state.loading = true;
      })
      .addCase(fetchProjects.fulfilled, (state, action) => {
        state.projects = action.payload;
        state.loading = false;
      })
      .addCase(fetchProjects.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      })
      // Create Project
      .addCase(createProject.pending, (state) => {
        state.loading = true;
      })
      .addCase(createProject.fulfilled, (state, action) => {
        console.log('Before push:', state.projects); // Log before push
        state.projects.push(action.payload);
        console.log('After push:', state.projects); // Log after push
        state.loading = false;
      })
      .addCase(createProject.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      });
  },
});

export const { setCurrentProject } = projectSlice.actions;
export default projectSlice.reducer;
