import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom'; // Import useNavigate
import { registerUserThunk } from '../redux/slices/authSlice';

const Register = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [name, setName] = useState('');
  const dispatch = useDispatch();
  const navigate = useNavigate(); // Initialize navigate
  const error = useSelector((state) => state.auth.error);

  const handleRegister = async () => {
    if (!name || !email || !password) {
      alert('All fields are required');
      return;
    }

    const registrationData = { name, email, password };
    console.log('Registration data:', registrationData);

    try {
      // Dispatch the registerUserThunk action with user registration data
      await dispatch(registerUserThunk(registrationData));

      // Navigate to the login page upon successful registration
      navigate('/login'); // Adjust the route as per your app structure
    } catch (error) {
      console.error('Registration failed:', error);
    }
  };

  return (
    <div>
      <h2>Register</h2>
      {error && <p style={{ color: 'red' }}>{error}</p>}
      <input
        type="text"
        placeholder="Name"
        value={name}
        onChange={(e) => setName(e.target.value)}
      />
      <input
        type="email"
        placeholder="Email"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
      />
      <input
        type="password"
        placeholder="Password"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
      />
      <button onClick={handleRegister}>Register</button>
    </div>
  );
};

export default Register;
