export const fetchProjectTasks = async (projectId) => {
    const response = await fetch(`${API_URL}/projects/${projectId}/tasks`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    });
    return response.json();
  };
