// src/components/KanbanBoard.js
import React from 'react';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { Paper, Typography, Box } from '@mui/material';
import { useDispatch } from 'react-redux';
import { updateTaskStatus } from '../redux/slices/taskSlice';
// Correct (named imports)
import { fetchProjectTasks, fetchProjects, loginUser } from '../api';


function KanbanBoard({ tasks, projectId }) {
  const dispatch = useDispatch();

  const statuses = ['To Do', 'In Progress', 'Done'];

  const onDragEnd = (result) => {
    const { source, destination, draggableId } = result;
    if (!destination) return;

    if (
      source.droppableId !== destination.droppableId ||
      source.index !== destination.index
    ) {
      const updatedStatus = destination.droppableId;
      dispatch(updateTaskStatus({ taskId: draggableId, status: updatedStatus }));

      // Update task status in the backend
      API.put(`/api/tasks/${draggableId}`, { status: updatedStatus });
    }
  };

  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
        {statuses.map((status) => (
          <Droppable droppableId={status} key={status}>
            {(provided) => (
              <Paper
                sx={{ width: '32%', minHeight: '500px', p: 2 }}
                ref={provided.innerRef}
                {...provided.droppableProps}
              >
                <Typography variant="h6" gutterBottom>
                  {status}
                </Typography>
                {tasks
                  .filter((task) => task.status === status)
                  .map((task, index) => (
                    <Draggable draggableId={task._id} index={index} key={task._id}>
                      {(provided) => (
                        <Paper
                          sx={{ p: 2, mb: 2 }}
                          ref={provided.innerRef}
                          {...provided.draggableProps}
                          {...provided.dragHandleProps}
                        >
                          <Typography variant="subtitle1">{task.name}</Typography>
                          <Typography variant="body2" color="textSecondary">
                            {task.description}
                          </Typography>
                        </Paper>
                      )}
                    </Draggable>
                  ))}
                {provided.placeholder}
              </Paper>
            )}
          </Droppable>
        ))}
      </Box>
    </DragDropContext>
  );
}

export default KanbanBoard;
