import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import axios from 'axios';
import jwtDecode from 'jwt-decode';

// Async thunk for user registration
export const registerUserThunk = createAsyncThunk(
  'auth/registerUser',
  async (userData, { rejectWithValue, dispatch }) => {
    try {
      const response = await axios.post('https://asana-backend.onrender.com/api/auth/register', userData);
      const token = response.data.token;

      // Decode the token to extract user information
      const decodedToken = jwtDecode(token);

      // Dispatch loginSuccess with the token
      dispatch(loginSuccess({ token }));

      return { token, user: decodedToken };
    } catch (err) {
      return rejectWithValue(err.response?.data?.message || 'Registration failed');
    }
  }
);

// Async thunk for user login
export const loginUserThunk = createAsyncThunk(
  'auth/loginUser',
  async (credentials, { rejectWithValue, dispatch }) => {
    try {
      const response = await axios.post('https://asana-backend.onrender.com/api/auth/login', credentials);
      const token = response.data.token;

      // Decode the token to extract user information
      const decodedToken = jwtDecode(token);

      // Dispatch loginSuccess with the token
      dispatch(loginSuccess({ token }));

      return { token, user: decodedToken };
    } catch (err) {
      return rejectWithValue(err.response?.data?.message || 'Login failed');
    }
  }
);

const authSlice = createSlice({
  name: 'auth',
  initialState: {
    user: null,
    token: null,
    loading: false,
    error: null,
  },
  reducers: {
    loginSuccess(state, action) {
      state.token = action.payload.token;

      // Decode token to extract user information
      try {
        const decodedToken = jwtDecode(action.payload.token);
        state.user = {
          _id: decodedToken.userId,
          role: decodedToken.role,
        };
      } catch (error) {
        console.error('Failed to decode token:', error);
        state.user = null;
      }
    },
    logout(state) {
      state.user = null;
      state.token = null;
    },
    setError(state, action) {
      state.error = action.payload;
    },
  },
  extraReducers: (builder) => {
    builder
      // Login User
      .addCase(loginUserThunk.pending, (state) => {
        state.loading = true;
      })
      .addCase(loginUserThunk.fulfilled, (state, action) => {
        state.loading = false;
        state.token = action.payload.token;
        state.user = action.payload.user;
      })
      .addCase(loginUserThunk.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      })
      // Register User
      .addCase(registerUserThunk.pending, (state) => {
        state.loading = true;
      })
      .addCase(registerUserThunk.fulfilled, (state, action) => {
        state.loading = false;
        state.token = action.payload.token;
        state.user = action.payload.user;
      })
      .addCase(registerUserThunk.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      });
  },
});

// Export actions
export const { loginSuccess, logout, setError } = authSlice.actions;

// Export the reducer
export default authSlice.reducer;

