import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { fetchProjects, createProject } from '../redux/slices/projectSlice';
import { logout } from '../redux/slices/authSlice';

const Dashboard = () => {
  const dispatch = useDispatch();
  const { projects = [], loading, error } = useSelector((state) => state.project); // Default to an empty array
  const { user, token } = useSelector((state) => state.auth); // Include token in case we need to check

  useEffect(() => {
    const fetchData = async () => {
      await dispatch(fetchProjects());
    };
    fetchData();
  }, [dispatch]);

  const handleCreateProject = async () => {
    // Log the entire state to debug
    console.log('Current Redux auth state:', { user, token });

    if (!user || !user._id) {
      console.error("User ID is not available.");
      return;
    }

    const projectData = {
      name: 'New Project', // Provide a name for the new project
      owner: user._id, // Use user ID from auth state
    };

    try {
      const resultAction = await dispatch(createProject(projectData));
      if (createProject.fulfilled.match(resultAction)) {
        console.log('Project created successfully:', resultAction.payload);
      } else {
        console.error('Failed to create project:', resultAction.error.message);
      }
    } catch (error) {
      console.error('Error creating project:', error);
    }
  };

  return (
    <div>
      <h1>Dashboard</h1>
      <button onClick={handleCreateProject}>Create Project</button>
      {loading && <p>Loading projects...</p>}
      {error && <p>Error: {error}</p>}
      <ul>
        {projects.length > 0 ? (
          projects.map((project) => (
            <li key={project._id}>{project.name}</li>
          ))
        ) : (
          <p>No projects available.</p>
        )}
      </ul>
    </div>
  );
};

export default Dashboard;
